//header

.hamburger{
    position: absolute;
    font-size: 3rem;
    top: 1.5rem;
    right: 1rem;
    
    
    :hover {
     background-color: $light-color;
    }
    span {
        padding: 1rem;
        border-radius: 50%;
    }
}

